import API from "../../../../api";
import axios from "axios";
import reducerActions from "./actions";
import TEXT_EDITOR_CONFIG from "../../../../components/TextEditor/config";
import MEDIA_CONTENT_TYPE from "../../../../variables/mediaContentType";
import {base64ToFile} from "../../../../functions/file";
import { serializeHtml, createPlateEditor, deserializeHtml, deserializeHtmlNode, deserializeHtmlElement, deserializeHtmlCodeBlock } from "@udecode/plate";

const CDN_URL = "https://server.lavenes.com:8085/"; //BRING IT TO GLOBAL VARIABLES
const fileBlobRegex = /data:image\//g;

const editor = createPlateEditor({
  plugins: TEXT_EDITOR_CONFIG.plugins,
  components: TEXT_EDITOR_CONFIG.components
});

const initState = {
    textEditorFocus: false,
    bannerImageBlob: null,
    bannerImageURL: null,
    categoryDropDownOptions: [],
    category: "",
    title: "",
    slug: "",
    keywords: "",
    privacy: "public",
    editId: "",
    description: "",
    content: [],
    defaultContent: [],
    editMode: false,
    loading: true,
    textEditorRef: null,
    privacyDropDownOptions: [
        {
            label: "Public",
            value: "public"
        },
        {
            label: "Hidden",
            value: "hidden"
        },
        {
            label: "Private",
            value: "private"
        }
    ]
};

class Controller {
    static state = initState;

    static reducer = (state, action) => { 
        let newState;

        switch(action.type) {
            //*VIEW ACTIONS
            case reducerActions.VIEW.SAVE_ARTICLE:
                newState = {...state};

                (async() => {
                    let postContent = [...newState.content];

                    //*Upload all media inside post content to server
                    await Promise.all(newState.content.map(async(item, i) => {
                        let content = {...item};

                        if(item.type == "img" && (!item.url?.includes(CDN_URL))) { //Is Image
                            let file;

                            if(fileBlobRegex.test(item.url)) { //Local File
                                file = base64ToFile(item.url, "file")
                            }else{ //Internet File
                                await axios.get(item.url, {
                                    responseType: 'arraybuffer'
                                }).then(response => {
                                    file = new File(
                                        [response.data], 
                                        item.url.split("/").pop(), 
                                        {type: "image/png"}
                                    );
                                });
                            }
    
                            await API.Files.Media.add(file, "post").then(res => {
                                content.url = res.data.url;
                            })
                        }

                        postContent[i] = content;
                    }));

                    let htmlContent = serializeHtml(editor, {nodes: postContent}); //Convert to html

                    if(!state.editMode) {//*CREATE
                        //Upload post
                        API.Post.add(
                            state.title,
                            state.slug,
                            state.keywords,
                            state.description,
                            state.privacy,
                            state.category,
                            newState.bannerImageURL,
                            htmlContent
                        ).then(result => {
                            alert("Upload success!");
                        })
                    }else{//*UPDATE
                        //Update post
                        API.Post.update(
                            state.editId,
                            state.title,
                            state.slug,
                            state.keywords,
                            state.description,
                            state.privacy,
                            state.category,
                            newState.bannerImageURL,
                            htmlContent
                        ).then(result => {
                            alert("Update success!");
                        })
                    }
                })();

                break;

            case reducerActions.VIEW.SET_LOADING:
                newState = {
                    ...state,
                    loading: action.payload || false
                }

                break;

            //*EDIT MODE ACTIONS
            case reducerActions.ARTICLE.SET_DATA: //*SET EDIT DATA
                let plateStateData = deserializeHtml(editor, {element: action.payload.content});

                newState = {
                    ...state,
                    editMode: true,
                    editId: action.payload._id,
                    title: action.payload.title,
                    slug: action.payload.slug,
                    keywords: action.payload.keywords,
                    description: action.payload.description,
                    category: action.payload.category,
                    content: plateStateData,
                    defaultContent: plateStateData,
                    bannerImageBlob: action.payload.banner,
                    bannerImageURL: action.payload.banner,
                    privacy: action.payload.status,
                };

                break;

            //*CATEGORY ACTIONS
            case reducerActions.CATEGORY.SET_DROPDOWN_OPTIONS:
                newState = {
                    ...state,
                    categoryDropDownOptions: action.payload,
                    category: action.payload[0].value,
                }
                
                break;

            case reducerActions.CATEGORY.SET_SELECTED_CATEGORY:
                newState = {
                    ...state,
                    category: action.payload
                }

                break;

            //*TEXT EDITOR ACTIONS
            case reducerActions.TEXT_EDITOR.ON_CHANGE:
                newState = {
                    ...state,
                    content: action.payload
                }

                break;

            //*DROPDOWN
            case reducerActions.DROPDOWN.CATEGORY_CHANGE:
                newState = {
                    ...state,
                    category: action.payload.value
                }

                break;

            case reducerActions.DROPDOWN.PRIVACY_CHANGE:
                newState = {
                    ...state,
                    privacy: action.payload.value
                }

                break;

            //*TEXTBOX
            case reducerActions.TEXTBOX.TITLE_CHANGE:
                newState = {
                    ...state,
                    title: action.payload,
                }

                break;

            case reducerActions.TEXTBOX.SLUG_CHANGE:
                newState = {
                    ...state,
                    slug: action.payload,
                }

                break;

            case reducerActions.TEXTBOX.KEYWORDS_CHANGE:
                newState = {
                    ...state,
                    keywords: action.payload,
                }

                break;

            case reducerActions.TEXTBOX.DESCRIPTION_CHANGE:
                newState = {
                    ...state,
                    description: action.payload,
                }

                break;
            
            //*INFORMATION ACTIONS
            case reducerActions.INFORMATION.BANNER_UPLOAD:
                newState = {
                    ...state,
                    bannerImageBlob: action.payload.blob,
                    bannerImageURL: action.payload.url,
                }

                break;

            default: 
                break;
        }

        return newState;
    };
}

export default Controller;

