import React, {useState, useEffect} from 'react';
import {Redirect} from 'react-router-dom';
import {Button, Textbox} from '../../../components/lavenes';
import API from "../../../api";
import APIVariables from '../../../api/variables';

var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

const LoginScreen = (props) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    useEffect(() => {
        if(localStorage.getItem("accessToken")) {
            window.location.href = '/#/quiz';
        }
    }, []);

    const handleLogin = async () => {
        setEmail(email.trim());

        if(email === '' || password === '') {
            //!TODO Custom Alert
            alert('Vui lòng nhập đầy đủ thông tin');
        }else if(!email.match(mailformat)) {
            alert("Email không đúng định dạng");
        }else{
            var result = await API.User.auth(email, password);
            
            if(result.code === 200) {
                var data = result.data;

                localStorage.setItem("accessToken", data.accessToken);
                localStorage.setItem("refreshToken", data.refreshToken);
                localStorage.setItem("companyId", data.companyId);
                localStorage.setItem("appId", data.companyId);
                localStorage.setItem("userId", data._id);
                localStorage.setItem("name", data.name);
                localStorage.setItem("email", data.email);
                localStorage.setItem("avatar", data.avatar);

                APIVariables.HEADERS.x_authorization = data.accessToken;
                APIVariables.HEADERS.app_id = data.companyId;

                console.log(APIVariables.HEADERS);

                //!TODO Redirect to home
                window.location.reload();
            }else{
                if(result.message == "EMAIL_NOT_FOUND") alert("Email không tồn tại");
                else if(result.message == "PASSWORD_NOT_MATCH") alert("Mật khẩu không đúng");
            }
        }
    }

    return(
        <div className="view fullsize center">
            <div className="form login">
                <h1>Đăng Nhập</h1>
                <input type="email" className="textbox" onChange={e => setEmail(e.target.value)} placeholder="Email hoặc tên đăng nhập"/>
                <input type="password"className="textbox" onChange={e => setPassword(e.target.value)} placeholder="Mật khẩu"/>
                <Button style={{marginTop: 16}} onClick={handleLogin}>Đăng Nhập</Button>
            </div>
        </div>
    )
}

export default LoginScreen;