import React, {useState} from 'react';
import { Navigate } from 'react-router-dom';
import "./styles.scss";

export const Header = (props) => {
    const {items, onAction} = props;
    const [goBackPath, setGoBackPath] = useState(null);

    const title = items.title;
    const leftItems = items.left;
    const rightItems = items.right;
    const centerItems = items.center;

    const handleGoBack = () => {
        let path = window.location.pathname;
        let pathArray = path.split('/');

        path = path.slice(0, path.length - pathArray[pathArray.length - 1].length - 1);

        setGoBackPath(path);
    };

    if(goBackPath) return <Navigate to={goBackPath}/>

    return(
        <article className="header-bar">
            <div className="col left">
                {leftItems.map((item, index) => {
                    const Icon = item.icon;

                    return(
                        <div className={`${item.type}-btn`} onClick={() => (item.type != "goback" ? onAction(item.action) : handleGoBack())} key={`left-action-btn-${index}`}>
                            <Icon className="icon"/>
                        </div>
                    )
                })}
                <span className="title">{title}</span>
            </div>
            <div className="col center">
                {centerItems.map((item, index) => {
                    const Icon = item.icon;
                    if(item.type == "spacing") return (
                        <div className="spacing" key={`center-action-btn-${index}`}>
                        </div>
                    )

                    return(
                        <div className="action-btn" onClick={() => onAction(item.action)} key={`center-action-btn-${index}`}>
                            <Icon className="icon"/>
                            <div className="tooltip">
                                <span className="label">{item.label}</span>
                            </div>
                        </div>
                    );
                })}
            </div>
            <div className="col right">
                {rightItems.map((item, index) => {
                    const Icon = item.icon;
                    return(
                        <div className="action-btn" onClick={() => onAction(item.action)} key={`right-action-btn-${index}`}>
                            <Icon className="icon"/>
                            <div className="tooltip">
                                <span className="label">{item.label}</span>
                            </div>
                        </div>
                    );
                })}
            </div>
        </article>
    )
}