import axios from "axios";
import APIVariables from "./variables";

class UserAPI {
    static async auth(email, password) {
        var result = await axios.post(`${APIVariables.API_URL}/users/auth`, {
            email,
            password
        }, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async verify() {
        var result = await axios.post(`${APIVariables.API_URL}/users/verify_access_token`, {
            accessToken: localStorage.getItem("accessToken")
        }, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }
}

export default UserAPI;