import axios from "axios";
import APIVariables from "../variables";

class QuizAPI {
    static async getAll() {
        var result = await axios.get(`${APIVariables.API_URL}/quizzes`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async get(id) {
        var result = await axios.get(`${APIVariables.API_URL}/quizzes/${id}`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async getCategory(categoryId) {
        var result = await axios.get(`${APIVariables.API_URL}/quizzes/category/${categoryId}`, {
            headers: APIVariables.HEADERS
        });

        console.log(result.data);

        return result.data;
    }
}

export default QuizAPI;