import React, {Component, useState} from 'react';
import {IoImageOutline} from "react-icons/io5";

import "./styles.scss";

export class Form extends Component {
    render() {
        return(
            <div className="form" style={this.props.style}>
                {this.props.children}
            </div>
        )
    }
}

export const FileDropArea = (props) => {
    const {onAction, id, style} = props;
    const [fileName, setFileName] = useState(null);

    const handleOnChange = (e) => {
        let action = {
            type: `ON_FILE_INPUT_CHANGE${id ? "#" + id.toUpperCase() : ""}`,
            payload: e.target.files[0]
        }

        setFileName(e.target.files[0].name);

        onAction(action);
    }

    return(
        <div className="form_cmp drop_area" style={style}>
            <label className="drop_content" htmlFor="file">
                <IoImageOutline className="icon"/>
                <span className={fileName ? "selected" : ""}>
                    {fileName || "Click to add an asset or drag and drop one in this area"}
                </span>
                <input type="file" id="file" onChange={handleOnChange}/>
            </label>
        </div>
    )
}

export const ViewFormLayout = (props) => {
    return(
        <div className="view_form_layout">
            {props.children}
        </div>
    )
}

export const GroupBox = (props) => {
    return(
        <div className="group_box" style={{
            boxShadow: `${props.noShadow ? "none" : "rgb(33 33 52 / 10%) 0px 1px 4px"}`,
            border: `${props.noBorder ? "none" : "1px solid rgb(234, 234, 239)"}`,
            ...props.style
        }}>
            {props.children}
        </div>
    )
}

export const FormInputLabel = (props) => {
    return(
        <span className="form_input_label">
            {props.children}
        </span>
    )
}