import React, {useReducer, useEffect} from 'react';
import reducerActions from './actions';
import API from "../../api";
import {View, Header, GalleryCardList, SectionTitle, Modal, Textbox, LoadingView} from "../../components/lavenes";

//*Init
import headerItems from "./headerItems";
import Controller from "./controller";

const CategoryScreen = (props) => {
    const [state, dispatch] = useReducer(Controller.reducer, Controller.state);
    const {type} = props;

    useEffect(() => {
        API.Category.getType(type).then(res => {
            console.log(res.data);
            dispatch({type: reducerActions.CATEGORY.SET_DATA, payload: res.data});
            dispatch({type: reducerActions.VIEW.SET_LOADING, payload: false});
        });
        
        dispatch({type: reducerActions.CATEGORY.SET_CATEGORY_TYPE, payload: type});
    }, [type]);

    //*ADD CATEGORY
    const handleAddCategory = async () => {
        let result = await API.Category.add(state.categoryTextboxValue, "active", state.categoryType);

        if(result.code === 200) {
            dispatch({type: reducerActions.CATEGORY.HIDE_POPUP});
            dispatch({
                type: reducerActions.CATEGORY.SET_DATA, 
                payload: [...state.categoriesData, result.data]
            });
            dispatch({
                type: reducerActions.CATEGORY.ON_TEXTBOX_CHANGE,
                payload: ""
            });
        }
    }

    return(
        <View>
            <Header items={headerItems} onAction={dispatch}/>
            {state.loading ? <LoadingView/> :
                <>
                    <SectionTitle title={`Feature ${type}`} label="What's new ?"/>
                    <GalleryCardList type={type} onAction={dispatch} items={state.categoriesData} addNewCard addNewCardLabel="Add new category" addNewCardId="category"/>
                    <Modal onSubmit={handleAddCategory} onAction={dispatch} visible={state.isCategoryPopupVisible} buttonLabel="Create" id="category">
                        <Textbox onAction={dispatch} value={state.categoryTextboxValue} placeholder="Category name" large id="category"/>
                    </Modal>
                </>
            }
        </View>
    )
}

export default CategoryScreen;