import React, {useReducer, useEffect} from "react";
import { useParams } from "react-router-dom";
import API from '../../../../api';
import reducerActions from "./actions";
import { usePlateEditorRef } from "@udecode/plate";
import {View, Header, Textbox, TextEditor, FormInputLabel, FileDropArea, LoadingView, ViewFormLayout, GroupBox, Select} from "../../../../components/lavenes";

//*Init
import headerItems from "./headerItems";
import Controller from "./controller";

const ArticleEditScreen = (props) => {
    const [state, dispatch] = useReducer(Controller.reducer, Controller.state);
    const {editMode} = props;
    const {categoryId,postId} = useParams();

    useEffect(() => {
        //*EDIT MODE
        if(editMode) {
            //Fetch post data
            API.Post.get(postId).then(result => {
                dispatch({
                    type: reducerActions.ARTICLE.SET_DATA,
                    payload: result.data
                });
            });
        }

        //*GET CATEGORY DROPDOWN OPTIONS
        API.Category.getType("post").then(res => {
            let options = res.data.map(item => {
                return {
                    value: item._id,
                    label: item.title
                }
            });

            dispatch({type: reducerActions.CATEGORY.SET_DROPDOWN_OPTIONS, payload: options});
            dispatch({type: reducerActions.CATEGORY.SET_SELECTED_CATEGORY, payload: categoryId});
            dispatch({type: reducerActions.VIEW.SET_LOADING, payload: false});
        });
    }, []);

    const handleUploadBanner = async(action) => {
        var fileBlob = URL.createObjectURL(action.payload);
        
        //Upload media
        API.Files.Media.add(action.payload, "post").then(result => {
            dispatch({
                type: reducerActions.INFORMATION.BANNER_UPLOAD,
                payload: {
                    blob: fileBlob,
                    url: result.data.url
                }
            });
        });
    }

    return (
        <View>
            <Header items={headerItems} onAction={dispatch}/>
            {state.loading ? <LoadingView/> :
                <ViewFormLayout>
                    <GroupBox>
                        <FormInputLabel>Banner</FormInputLabel>
                        <FileDropArea 
                            id="BANNER_UPLOAD" 
                            onAction={handleUploadBanner}
                            style={{
                                backgroundImage: state.bannerImageBlob ? `url(${state.bannerImageBlob})` : 'none',
                            }}
                        />

                        <FormInputLabel>Title</FormInputLabel>
                        <Textbox defaultValue={state.title} onAction={dispatch} id="TITLE" placeholder="Post Title"/>

                        <FormInputLabel>Slug</FormInputLabel>
                        <Textbox defaultValue={state.slug} onAction={dispatch} id="SLUG" placeholder="post-title"/>

                        <FormInputLabel>Keywords</FormInputLabel>
                        <Textbox defaultValue={state.keywords} onAction={dispatch} id="KEYWORDS" placeholder="hello, world, 2022"/>

                        <FormInputLabel>Description</FormInputLabel>
                        <Textbox defaultValue={state.description} onAction={dispatch} id="DESCRIPTION" placeholder="Your description"/>

                        <FormInputLabel>Category</FormInputLabel>
                        <Select defaultValue={state.category} onAction={dispatch} options={state.categoryDropDownOptions} id="CATEGORY"/>

                        <FormInputLabel>Privacy</FormInputLabel>
                        <Select defaultValue={state.privacy} onAction={dispatch} id="PRIVACY" options={state.privacyDropDownOptions}/>
                    </GroupBox>
                    <GroupBox noShadow noBorder style={{overflow: "hidden", paddingLeft: 3, paddingRight: 0, paddingBottom: 0, paddingTop: 0}}>
                        <TextEditor value={state.content} onAction={dispatch} placeholder="Enter something..." id="MAIN"/>
                    </GroupBox>
                </ViewFormLayout>
            }
        </View>
    );
}

export default ArticleEditScreen;