import React from 'react';
import "./styles.scss";

export const Button = (props) => {
    const label = props.children; //Get children text
    const size = props.size || ''; //Get button type large small
    const type = props.type || '';
    const disabled = props.disabled ? 'disabled' : '';
    const styles = props.style || null;

    return(
        <button className={`button ${size} ${type} ${disabled}`} disabled={props.disabled ? 'disabled' : ''} style={styles} onClick={props.onClick}>
            <span className="label">{label}</span>
        </button>
    )
}