import {useState} from 'react';
import {findWithAttr} from '../../functions/Global';

export default class Controller {
    Main = (props) => {
        const options = props.options;
        const [optionVisible, setOptionVisible] = useState(false);
        const [selected, setSelected] = useState(props.defaultValue ? findWithAttr(options, 'value', props.defaultValue) > -1 ? findWithAttr(options, 'value', props.defaultValue) : 0 : 0);

        const showOption = () => setOptionVisible(!optionVisible);

        //Select option
        const select = (data, index) => {
            setSelected(index);
            setOptionVisible(false);

            try {
                props.onAction({
                    type: 'ON_SELECT_CHANGE#'+props.id,
                    payload: data
                }); //Return onChange data
            }catch(e) {
                console.log(e);
            }
        }

        return {
            optionVisible,
            showOption,
            selected,
            setOptionVisible,
            select
        }
    }
}