import MEDIA_CONTENT_TYPE from "../variables/mediaContentType";

export const base64ToFile = (base64, filename) => {
    let contentType = base64.split(";")[0].replaceAll("data:", "");

    var arr = base64.split(','),
        bstr = atob(arr[1]), 
        n = bstr.length, 
        u8arr = new Uint8Array(n);

    while(n--){
        u8arr[n] = bstr.charCodeAt(n);
    }

    return new File(
        [u8arr],
        filename + MEDIA_CONTENT_TYPE[contentType],
        {
            type: contentType
        }
    );
}