import * as Plate from '@udecode/plate'

const resetBlockTypesCommonRule = {
  types: [Plate.ELEMENT_BLOCKQUOTE, Plate.ELEMENT_TODO_LI],
  defaultType: Plate.ELEMENT_PARAGRAPH,
}

const TEXT_EDITOR_CONFIG = {
    plugins : [
        // elements
        Plate.createParagraphPlugin(),      // paragraph element
        Plate.createBlockquotePlugin(),     // blockquote element
        Plate.createCodeBlockPlugin(),      // code block element
        Plate.createHeadingPlugin(),        // heading elements
        Plate.createListPlugin(),           //List
    
        // basic
        Plate.createBasicElementsPlugin(), 
        Plate.createResetNodePlugin({
          options: {
            rules: [
              {
                ...resetBlockTypesCommonRule,
                hotkey: 'Enter',
                predicate: Plate.isBlockAboveEmpty,
              },
              {
                ...resetBlockTypesCommonRule,
                hotkey: 'Backspace',
                predicate: Plate.isSelectionAtBlockStart,
              },
            ],
          },
        }),
        Plate.createSoftBreakPlugin({
          options: {
            rules: [
              { hotkey: 'shift+enter' },
              {
                hotkey: 'enter',
                query: {
                  allow: [Plate.ELEMENT_CODE_BLOCK, Plate.ELEMENT_BLOCKQUOTE, Plate.ELEMENT_TD],
                },
              },
            ],
          },
        }),
        Plate.createExitBreakPlugin({
          options: {
            rules: [
              {
                hotkey: 'mod+enter',
              },
              {
                hotkey: 'mod+shift+enter',
                before: true,
              },
              {
                hotkey: 'enter',
                query: {
                  start: true,
                  end: true,
                  allow: Plate.KEYS_HEADING,
                },
              },
            ],
          },
        }),
        Plate.createIndentPlugin({
          inject: {
            props: {
              validTypes: [
                Plate.ELEMENT_PARAGRAPH,
                Plate.ELEMENT_H1
              ],
            },
          },
        }),
        Plate.createTrailingBlockPlugin({ type: Plate.ELEMENT_PARAGRAPH }),
        Plate.createSelectOnBackspacePlugin({
          options: {
            query: {
              allow: [Plate.ELEMENT_IMAGE, Plate.ELEMENT_HR],
            },
          },
        }),
    
        // marks
        Plate.createBoldPlugin(),           // bold mark
        Plate.createItalicPlugin(),         // italic mark
        Plate.createUnderlinePlugin(),      // underline mark
        Plate.createStrikethroughPlugin(),  // strikethrough mark
        Plate.createCodePlugin(),           // code mark
    
        // aligns
        Plate.createAlignPlugin(),          //Aligns
    
        //inline
        Plate.createLinkPlugin(),           //Link
        Plate.createFontColorPlugin(),
        Plate.createFontBackgroundColorPlugin(),
    
        // medias
        Plate.createMediaEmbedPlugin(),     //Video
        Plate.createImagePlugin(),          //Image
    ],
    components: Plate.createPlateUI()
}

export default TEXT_EDITOR_CONFIG;