import APIVariables from '../../api/variables';
import reducerActions from "./actions";

class Controller {
    static state = {
        viewType: "grid",
        redirectToAddPost: false,
        quizData: [],
        postEditId: null,
        loading: true
    }

    static reducer = (state, action) => {
        let newState = {...state};

        switch (action.type) {
            case reducerActions.VIEW.SET_VIEW_TYPE:
                newState.viewType = action.payload;

                break;

            case reducerActions.VIEW.SET_LOADING:
                newState.loading = action.payload;

                break;

            //*QUIZ
            case reducerActions.QUIZ.SET_DATA:
                newState.quizData = action.payload;

                break;

            case reducerActions.QUIZ.ON_CLICK:
                window.open(`${APIVariables.API_URL}/quiz_answers/export/${action.payload}`, '_blank').focus();

                break;

            default:
                break;
        }

        return newState;
    }
}

export default Controller;