import React from 'react';
import {IoChevronForward, IoChevronBack} from 'react-icons/io5';
import Controller from './controller';
import "./styles.scss";

export const DatePicker = (props) => {
    const {calendarData, focusDate, dateOnClick, setTab, tab, monthData} = new Controller().Main(props);

    if(!calendarData) return <div></div>

    return(
        <div className="date-picker">
            <div className="textbox">
                <input/>
            </div>
            <div className="calendar">
                <div className="header">
                    <div className="date-container-s">
                        <span className="date" onClick={() => setTab(0)}>{focusDate > 0 ? focusDate : new Date().getDate()}</span>
                        <span className="date" onClick={() => setTab(1)}> tháng 6,</span>
                        <span className="date" onClick={() => setTab(2)}> 2021</span>    
                    </div>
                    <div className="navigation-btn">
                        <IoChevronBack className="icon"/>
                    </div>
                    <div className="navigation-btn">
                        <IoChevronForward className="icon"/>
                    </div>
                </div>
                {tab === 0 ? 
                    <div className="date-container">
                        <span className="label">Th 2</span>
                        <span className="label">Th 3</span>
                        <span className="label">Th 4</span>
                        <span className="label">Th 5</span>
                        <span className="label">Th 6</span>
                        <span className="label">Th 7</span>
                        <span className="label">CN</span>
                    </div> : null}
                <div className="day-container month">
                    {
                        tab === 0 ?
                        calendarData.map((data, i) => {
                            return (
                                <div className={`day ${data === focusDate ? 'focused' : data === new Date().getDate() ? 'now' : ''}`} onClick={() => dateOnClick(data)}>
                                    <span className="label" key={`day-label-${i}`}>{data}</span>
                                </div>
                            )
                        }) : tab === 1 ? 
                        monthData.map((data, i) => {
                            return (
                                <div className={`day ${data === focusDate ? 'focused' : data === new Date().getDate() ? 'now' : ''}`} onClick={() => dateOnClick(data)}>
                                    <span className="label" key={`day-label-${i}`}>Th {data}</span>
                                </div>
                            )
                        }) :
                        calendarData.map((data, i) => {
                            return (
                                <div className={`day ${data === focusDate ? 'focused' : data === new Date().getDate() ? 'now' : ''}`} onClick={() => dateOnClick(data)}>
                                    <span className="label" key={`day-label-${i}`}>{data}</span>
                                </div>
                            )
                        }) 
                    }
                </div>
            </div>
        </div>
    )
}