import React, {useReducer, useEffect} from 'react';
import reducerActions from './actions';
import { Navigate, useParams } from 'react-router-dom';
import API from "../../../api";
import {View, Header, PostCardList, SectionTitle, LoadingView} from "../../../components/lavenes";

//*Init
import headerItems from "./headerItems";
import Controller from "./controller";

const ArticleScreen = (props) => {
    const [state, dispatch] = useReducer(Controller.reducer, Controller.state);
    const {id} = useParams();

    useEffect(() => {
        API.Post.getCategory(id).then(res => {
            dispatch({
                type: reducerActions.ARTICLE.SET_DATA,
                payload: res.data
            });

            dispatch({
                type: reducerActions.VIEW.SET_LOADING,
                payload: false
            });
        });
    },[]);

    if(state.redirectToAddPost && state.postEditId) return <Navigate to={`/articles/${id}/edit/${state.postEditId}`}/>
    if(state.redirectToAddPost) return <Navigate to={`/articles/${id}/add`}/>

    return (
        <View>
            <Header onAction={dispatch} items={headerItems} />
            {state.loading ? <LoadingView/> :
                <>
                    <SectionTitle title={`Feature Articles`} label="What's new ?"/>
                    <PostCardList 
                        onAction={dispatch} 
                        items={state.articlesData} 
                        addNewCard 
                        addNewCardLabel="Add new category" 
                        addNewCardId="ARTICLE"
                    />
                </>
            }
        </View>
    );
}

export default ArticleScreen;