import axios from "axios";
import APIVariables from "../variables";

class CategoryAPI {
    static async getAll() {
        var result = await axios.get(`${APIVariables.API_URL}/categories`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async getType(type) {
        var result = await axios.get(`${APIVariables.API_URL}/categories/type/${type}`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async get(id) {
        var result = await axios.get(`${APIVariables.API_URL}/categories/${id}`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async add(title, status, type) {
        var result = await axios.post(`${APIVariables.API_URL}/categories`, {
            title,
            status,
            type
        }, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }
}

export default CategoryAPI;