import axios from "axios";
import APIVariables from "../../variables";

class MediaAPI {
    static async add(file, type) {
        const formData = new FormData(); 
        formData.append( 
          "file", 
          file, 
          file.name 
        ); 

        var result = await axios.post(`${APIVariables.API_URL}/upload/media/${type}`, formData, {
            headers: {
                ...APIVariables.HEADERS, 
                "Content-Type": "multipart/form-data"
            }
        });

        return result.data;
    }
}

export default MediaAPI;