import reducerActions from "./actions";

class Controller {
    static state = {
        viewType: "grid",
        redirectToAddPost: false,
        articlesData: [],
        postEditId: null,
        loading: true
    }

    static reducer = (state, action) => {
        let newState = {...state};

        switch (action.type) {
            case reducerActions.VIEW.SET_VIEW_TYPE:
                newState.viewType = action.payload;

                break;

            case reducerActions.VIEW.SET_LOADING:
                newState.loading = action.payload;

                break;

            case reducerActions.ARTICLE.ADD:
                newState.redirectToAddPost = true;
                
                break;

            

            //*ARTICLES
            case reducerActions.ARTICLE.SET_DATA:
                newState.articlesData = action.payload;

                break;

            case reducerActions.ARTICLE.ON_CLICK:
                newState.redirectToAddPost = true;
                newState.postEditId = action.payload;

                break;
            default:
                break;
        }

        return newState;
    }
}

export default Controller;