import reducerActions from "./actions";
import API from "../../api";

const initState = {
    viewType: "list",
    redirectToAddPost: false,
    featureCategoriesData: [],
    categoriesData: [],
    categoryType: "",

    //*CATEGORY
    isCategoryPopupVisible: false,
    categoryTextboxValue: "",
    loading: true
};

class Controller {
    static state = initState;

    static reducer = (state, action) => { 
        let newState = {...state};

        switch(action.type) {
            case reducerActions.VIEW.SET_VIEW_TYPE:
                newState = {
                    ...state,
                    viewType: action.payload,
                }
                break;

            case reducerActions.VIEW.SET_LOADING:
                newState = {
                    ...state,
                    loading: action.payload,
                }

                break;

            case reducerActions.VIEW.ADD_NEW_ARTICLE:
                // newState = {
                //     ...state,
                //     redirectToAddPost: true,
                // }
                break;
            case reducerActions.VIEW.SEARCH_ARTICLE:
                break;
            //*CATEGORY
            case reducerActions.CATEGORY.SET_DATA: //Set categories data
                newState = {
                    ...state,
                    categoriesData: action.payload,
                }

                break;
            case reducerActions.CATEGORY.SET_CATEGORY_TYPE: //Set category type
                newState = {
                    ...state,
                    categoryType: action.payload,
                }

                break;
            case reducerActions.CATEGORY.SHOW_POPUP: //Show popup
                newState = {
                    ...state,
                    isCategoryPopupVisible: true,
                }

                break;
            case reducerActions.CATEGORY.HIDE_POPUP: //Hide popup
                newState = {
                    ...state,
                    isCategoryPopupVisible: false,
                }   

                break;
            case reducerActions.CATEGORY.ON_TEXTBOX_CHANGE: //Textbox category change
                newState = {
                    ...state,
                    categoryTextboxValue: action.payload,
                }

                break;
            default: 
                throw Error("Invalid action type");
                break;
        }

        return newState;
    };
}

export default Controller;

