const API = localStorage.getItem('api_domain');
const SERVER_LINK = "http://localhost:8088";
const DEBUG = false;

function findWithAttr(array, attr, value) {
    for(var i = 0; i < array.length; i += 1) {
        if(array[i][attr] === value) {
            return i;
        }
    }
    return -1;
}

function dateTransform(date, construct) {
    let n_date = new Date(date);
    let Hours = n_date.getHours();
    let Day = n_date.getDate();
    let Days = n_date.getDay() + 1;
    let FullYear = n_date.getFullYear();
    let Month = n_date.getMonth() + 1;
    let Minutes = n_date.getMinutes();

    let newConstruct = construct;

    newConstruct = newConstruct.replace(/{yy}/g, FullYear.toString().slice(2));
    newConstruct = newConstruct.replace(/{yyyy}/g, FullYear);
    newConstruct = newConstruct.replace(/{m}/g, Month);
    newConstruct = newConstruct.replace(/{mm}/g, Month <= 9 ? '0' + Month : Month);
    newConstruct = newConstruct.replace(/{d}/g, Day);
    newConstruct = newConstruct.replace(/{dd}/g, Day <= 9 ? '0' + Day : Day);
    newConstruct = newConstruct.replace(/{dy}/g, Days);
    newConstruct = newConstruct.replace(/{dy}/g, Days <= 9 ? '0' + Days : Days);
    newConstruct = newConstruct.replace(/{h}/g, Hours);
    newConstruct = newConstruct.replace(/{hh}/g, Hours <= 9 ? '0' + Hours : Hours);
    newConstruct = newConstruct.replace(/{mt}/g,Minutes);
    newConstruct = newConstruct.replace(/{mt}/g,Minutes <= 9 ? '0' + Minutes : Minutes);

    return newConstruct;
}

function deleteInObject(array, attr, value) {
    array.splice(findWithAttr(array, attr, value), 1);

    return true;
}

function randomStr(length) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
       result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
 }

 function removeVietnameseTones(str) {
     str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
     str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
     str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
     str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
     str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
     str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
     str = str.replace(/đ/g, "d");
     str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, "A");
     str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, "E");
     str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, "I");
     str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, "O");
     str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, "U");
     str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, "Y");
     str = str.replace(/Đ/g, "D");
     // Some system encode vietnamese combining accent as individual utf-8 characters
     // Một vài bộ encode coi các dấu mũ, dấu chữ như một kí tự riêng biệt nên thêm hai dòng này
     str = str.replace(/\u0300|\u0301|\u0303|\u0309|\u0323/g, ""); // ̀ ́ ̃ ̉ ̣  huyền, sắc, ngã, hỏi, nặng
     str = str.replace(/\u02C6|\u0306|\u031B/g, ""); // ˆ ̆ ̛  Â, Ê, Ă, Ơ, Ư
     // Remove extra spaces
     // Bỏ các khoảng trắng liền nhau
     str = str.replace(/ + /g, " ");
     str = str.trim();
     // Remove punctuations
     // Bỏ dấu câu, kí tự đặc biệt
     str = str.replace(/!|@|%|\^|\*|\(|\)|\+|\=|\<|\>|\?|\/|,|\.|\:|\;|\'|\"|\&|\#|\[|\]|~|\$|_|`|-|{|}|\||\\/g, " ");
     return str;
 }

export {
    findWithAttr,
    deleteInObject,
    API,
    randomStr,
    SERVER_LINK,
    DEBUG,
    dateTransform,
    removeVietnameseTones
}