import React from 'react';
import {Link} from 'react-router-dom';
import "./styles.scss";
import {IoAdd} from 'react-icons/io5';

export const PostCard = (props) => {
    const {id, title, date, description, thumbnail, onAction} = props;

    const handleOnClick = () => {
        onAction({
            type: "ON_CARD_CLICK",
            payload: id
        });
    }

    return(
        <div className="card post" onClick={handleOnClick}>
            <div className="thumbnail" style={{backgroundImage: `url(${thumbnail})`}}></div>
            <div className="information">
                <div className="title-container">
                    <span className="title">{title}</span>
                    <span className="date">{date}</span>
                </div>
                <p className="description">{description}</p>
            </div>
        </div>
    )
}

export const PostCardList = (props) => {
    const {items, addNewCard, addNewCardLabel, onAction, addNewCardId} = props;

    return(
        <div className={`card-list post-list ${!items?.length ? "list-null" : ""}`}>
            {items?.map((item, index) => {
                return <PostCard 
                    id={item._id} 
                    key={`post-card-${index}`} 
                    thumbnail={item.banner} 
                    date={item.date} 
                    title={item.title} 
                    description={item.description}
                    onAction={onAction}
                    />
            })}

            {addNewCard ? <AddNewCard label={addNewCardLabel} onAction={onAction} id={addNewCardId}/> : null} 
        </div>
    )
}

export const ImageCard = (props) => {
    return(
        <article className="card image">
            <div className="thumbnail">
                <div className="title-container">
                    <span className="title">{props.title}</span>
                    <span className="date">{props.date}</span>
                </div>
            </div>
        </article>
    )
}

export const ProductCard = (props) => {
    return(
        <article className="card product">
            <div className="thumbnail"></div>
            <div className="information">
                <div className="title-container">
                    <span className="title">{props.title}</span>
                    <span className="price">{props.price}</span>
                </div>
                <p className="description">{props.description}</p>
                <span className="date">{props.date}</span>
            </div>
        </article>
    )
}

export const GalleryCard = (props) => {
    const {title, items, id, type} = props;

    return(
        <Link to={`/${type}/${id}`} className="card gallery">
            <div className="title-container">
                <span className="title">{title}</span>
                <span className="count">{items?.length || 0}</span>
            </div>
            {items?.length ? 
                <>
                <div className="preview">
                    <div className="thumbnail" style={{backgroundImage: `url(${items[0]?.banner})`}}></div>
                    <div className="thumbnail" style={{backgroundImage: `url(${items[1]?.banner})`}}></div>
                    <div className="thumbnail" style={{backgroundImage: `url(${items[2]?.banner})`}}></div>
                    <div className="thumbnail" style={{backgroundImage: `url(${items[3]?.banner})`}}></div>
                </div>
                </>
                :
                <div className="preview">
                    <div className="thumbnail"></div>
                    <div className="thumbnail"></div>
                    <div className="thumbnail"></div>
                    <div className="thumbnail"></div>
                </div>
            }
        </Link>
    )
}

export const GalleryCardList = (props) => {
    const {items, addNewCard, addNewCardLabel, onAction, addNewCardId, type} = props;

    return(
        <div className={`card-list gallery-list ${!items?.length ? "list-null" : ""}`}>
            {items?.map((item, index) => {
                return <GalleryCard 
                    id={item._id} 
                    key={`gallery-card-${index}`} 
                    items={item.children}
                    title={item.title} 
                    type={type}
                />
            })}

            {addNewCard ? <AddNewCard label={addNewCardLabel} onAction={onAction} id={addNewCardId}/> : null} 
        </div>
    )
}

export const AddNewCard = (props) => {
    const handleClick = () => {
        let action = {
            type: `ADD_NEW_CARD_CLICKED${props.id ? "#" + props.id.toUpperCase() : ""}`,
        }

        props.onAction(action);
    }

    return(
        <article className="card addnew" onClick={handleClick}>
            <IoAdd className="icon"/>
            <span className="badge">
                {props.label}
            </span>
        </article>
    )
}