import React, { Component } from 'react';
import { Routes, Route, Navigate, Outlet } from 'react-router-dom';

//COMPONENTS
import {SideBar} from './components/Sidebar'; 

//VIEWS
import Home from './views/Home/index';
import Login from './views/Auth/Login';
import CategoryScreen from './views/Category';
import ArticleEditScreen from './views/Posts/Article/Edit';
import ArticleScreen from './views/Posts/Article';
import QuizScreen from './views/Quiz';

const PrivateRoute = ({ component: Component, ...rest }) => {
  if(localStorage.getItem("accessToken") && localStorage.getItem("refreshToken")) {
    return (
      <Outlet/>
    );
  }

  console.log("LOGIN");

  return (
    <Route {...rest} render={ props => <Navigate to='/login' />} />
  );
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state ={
    }
  }

  render() {
    if(localStorage.getItem("accessToken") && localStorage.getItem("refreshToken")) {
      return(
        <div>
          <SideBar/>
          <Main/>
        </div>
      )
    }

    return (
      <div>
        <Main />
      </div>
    );
  }
}

class Main extends Component {
  constructor(props) {
    super(props);
  }

  render() {
      return (
        <main>
          <Routes>
            <Route exact path='/login' element={<Login />}/>
            <Route exact path='/articles/:categoryId/add' element={<PrivateRoute/>}>
              <Route exact path='/articles/:categoryId/add' element={<ArticleEditScreen/>}/>
            </Route>
            <Route exact path='/articles/:categoryId/edit/:postId' element={<PrivateRoute/>}>
              <Route exact path='/articles/:categoryId/edit/:postId' element={<ArticleEditScreen editMode/>}/>
            </Route>
            <Route exact path='/post/:id' element={<PrivateRoute/>}>
              <Route exact path='/post/:id' element={<ArticleScreen/>}/>
            </Route>
            <Route exact path='/post' element={<PrivateRoute/>}>
              <Route exact path='/post' element={<CategoryScreen type="post"/>}/>
            </Route>
            <Route exact path='/quiz/:id' element={<PrivateRoute/>}>
              <Route exact path='/quiz/:id' element={<QuizScreen/>}/>
            </Route>
            <Route exact path='/quiz' element={<PrivateRoute/>}>
              <Route exact path='/quiz' element={<CategoryScreen type="quiz"/>}/>
            </Route>
            <Route exact path='/home' element={<PrivateRoute/>}>
              <Route exact path='/home' element={<Home/>}/>
            </Route>
          </Routes>
        </main>
      )
  }
}

export default App;