import React from 'react';
import {ToolbarPlugin} from "./plugins/ToolbarPlugin";
import { MdFormatBold, MdFormatItalic, MdFormatUnderlined } from 'react-icons/md';
import TEXT_EDITOR_CONFIG from './config';
import * as Plate from '@udecode/plate'

import "./styles.scss";

export default function TextEditor(props) {
  const {id, onAction, value} = props;

  // try to remove some plugins!TEXT_EDITOR_CONFIG
  const plugins = Plate.createPlugins(TEXT_EDITOR_CONFIG.plugins, {
    // Plate components
    components: TEXT_EDITOR_CONFIG.components
  });

  const handleOnChange = (value) => {
    onAction({
      type: "ON_TEXT_EDITOR_CHANGE#" + id,
      payload: value
    });
  }

  const BallonToolbarMarks = () => {
    const editor = Plate.usePlateEditorRef();

    const arrow = false;
    const theme = 'light';
    const popperOptions = {
      placement: 'top'
    };

    return (
      <Plate.BalloonToolbar
        popperOptions={popperOptions}
        theme={theme}
        arrow={arrow}
      >
        <Plate.MarkToolbarButton
          type={Plate.getPluginType(editor, Plate.MARK_BOLD)}
          icon={<MdFormatBold className="icon"/>}
        />
        <Plate.MarkToolbarButton
          type={Plate.getPluginType(editor, Plate.MARK_ITALIC)}
          icon={<MdFormatItalic className="icon"/>}
        />
        <Plate.MarkToolbarButton
          type={Plate.getPluginType(editor, Plate.MARK_UNDERLINE)}
          icon={<MdFormatUnderlined className="icon"/>}
        />
      </Plate.BalloonToolbar>
    );
  };


  return (
    <div className="editor-container">
      <ToolbarPlugin editorId={`PLATE#${id}`}/>
      <Plate.Plate
        id={`PLATE#${id}`}
        initialValue={value}
        onChange={handleOnChange}
        editableProps={{
          placeholder:"Type something...",
          spellCheck: false,
          className: "text-editor"
        }}
        plugins={plugins}
      >
        <BallonToolbarMarks/>
      </Plate.Plate>
    </div>
  );
}
