import React from 'react';
import {Button} from '../Button';
import "./styles.scss";

export const Alert = (props) => {
    const Icon = props.icon;
    const visible = props.visible ? 'visible' : '';
    const styles = props.styles || null;

    return (
        <div className={`backdrop ${visible}`} onClick={props.onClose}>
            <div className="popup alert" style={styles}>
                <Icon className="icon"/>
                <span className="title">{props.title}</span>
                <span className="msg">{props.msg}</span>
                <Button size="large" onClick={props.onClick}>{props.buttonLabel}</Button>
            </div>
        </div>
    )
}

export const Modal = (props) => {
    const {id, onAction, onSubmit} = props;

    const visible = props.visible ? 'visible' : '';
    const styles = props.styles || null;

    const handleCloseModal = () => {
        let action = {
            type: `HIDE_POPUP${id ? "#" + id.toUpperCase() : ""}`,
        }

        onAction(action);
    }

    const handleSubmitModal = () => {
        onSubmit();
    }

    return (
        <div className={`backdrop ${visible}`} onClick={props.onClose}>
            <div className="popup modal" style={styles}>
                <span className="title">{props.title}</span>
                <span className="msg">{props.msg}</span>
                {props.children}
                <div className="action-container">
                    <Button size="large" type="secondary" className="cancel" onClick={handleCloseModal}>Cancel</Button>
                    <Button size="large" onClick={handleSubmitModal}>{props.buttonLabel}</Button>
                </div>
            </div>
        </div>
    )
}