import axios from "axios";
import APIVariables from "../variables";

class PostAPI {
    static async getAll() {
        var result = await axios.get(`${APIVariables.API_URL}/posts`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async getCategory(categoryId) {
        var result = await axios.get(`${APIVariables.API_URL}/posts/category/${categoryId}`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async getSlug(slug) {
        var result = await axios.get(`${APIVariables.API_URL}/posts/slug/${slug}`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async get(id) {
        var result = await axios.get(`${APIVariables.API_URL}/posts/${id}`, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async add(title, slug, keywords, description, status, category, banner, content) {
        var result = await axios.post(`${APIVariables.API_URL}/posts`, {
            title,
            slug,
            keywords,
            description,
            status,
            category,
            banner,
            content
        }, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }

    static async update(id, title, slug, keywords, description, status, category, banner, content) {
        var result = await axios.patch(`${APIVariables.API_URL}/posts/${id}`, {
            title,
            slug,
            keywords,
            description,
            status,
            category,
            banner,
            content
        }, {
            headers: APIVariables.HEADERS
        });

        return result.data;
    }
}

export default PostAPI;