import React, {useRef} from 'react';
import "./styles.scss";


export const Textbox = (props) => {
    const {disabled, large, style, id, onAction, value} = props;

    const handleOnChange = (e) => {
        let action = {
            type: `ON_TEXTBOX_CHANGE${id ? "#" + id.toUpperCase() : ""}`,
            payload: e.target.value
        }

        onAction(action);
    }

    return(
        <div style={style} className={`textbox ${disabled ? 'disabled' :  ''} ${large ? 'large' : ''}`}>
            <input value={value} className="input" placeholder={props.placeholder} onChange={handleOnChange} onKeyPress={props.onKeyPress} onKeyDown={props.onKeyDown} onKeyUp={props.onKeyUp}  defaultValue={props.defaultValue}/>
        </div>
    )
}

export const Textarea = (props) => {
    const {disabled, large} = props;

    return(
        <textarea className={`textbox ${disabled ? 'disabled' :  ''} ${large ? 'large' : ''}`} placeholder={props.placeholder} onChange={props.onChange} onKeyPress={props.onKeyPress} onKeyDown={props.onKeyDown} onKeyUp={props.onKeyUp}>
            {props.children}
        </textarea>
    )
}