import React, {Component} from 'react';
import {BsReverseLayoutSidebarReverse} from 'react-icons/bs';
import {IoHomeOutline, IoFileTrayFullOutline} from 'react-icons/io5';
import {Link, NavLink} from 'react-router-dom';

//Menu Items
import menuItems from "./menuItems";

//STYLES
import "./styles.scss";

export const SideBar = () => {
    return(
        <div className="sidebar">
            <div className="header">
                <div className="user">
                    <div className="avatar" style={{backgroundImage: `url(${localStorage.getItem("avatar")})`}}></div>
                    <span className="name">{localStorage.getItem("name")}</span>
                </div>
                <div className="action-btn">
                    <BsReverseLayoutSidebarReverse className="icon"/>
                </div>
            </div>
            <MenuList/>
        </div>
    )
}

const NavItem = (props) => {
    const Icon = props.item.icon;

    return (
        <NavLink to={props.item.url} activeClassName='active' className="item">
            <div className="icon">
                <Icon/>
            </div>
            <div className="title">
                {props.item.title}
            </div>
        </NavLink>
    );
}

const NavGroup = (props) => {
    return (
        <div className="menu_item_container">
            <div className="group-title">
                <span className="title">{props.item.title}</span>
            </div>
            <div className="group-items">
                {props.item.children.map((item, index) => {
                    switch (item.type) {
                        case 'item':
                            return <NavItem key={item.id} item={item} />;
                        default:
                            break;
                    }
                })}
            </div>
            <hr/>
        </div>
    );
}

const MenuList = () => {
    return menuItems.map((item, index) => {
        switch (item.type) {
            case 'group':
                return <NavGroup key={item.id} item={item} />;
            default:
                break;
        }
    });
}